const jwt = require('jsonwebtoken');

// Enhanced logging function
const log = (...args) => {
  console.log('[Auth Middleware]', ...args);
};

// Helper function to extract token from request
const getTokenFromRequest = (event) => {
  // Check Authorization header first
  const authHeader = event.headers.authorization || '';
  if (authHeader.startsWith('Bearer ')) {
    return authHeader.substring(7);
  }
  
  // Check cookies if not in header
  const cookies = event.headers.cookie || '';
  const tokenCookie = cookies.split('; ').find(c => c.startsWith('token='));
  if (tokenCookie) {
    return tokenCookie.split('=')[1];
  }
  
  return null;
};

/**
 * Middleware to verify JWT token
 * @param {Object} event - Netlify function event
 * @param {Object} context - Netlify function context
 * @returns {Object|null} - User ID if authenticated, null otherwise
 */
const verifyToken = (event, context) => {
  try {
    const token = getTokenFromRequest(event);
    
    if (!token) {
      log('No token provided');
      return null;
    }
    
    // Verify token
    const decoded = jwt.verify(token, process.env.JWT_SECRET || 'your-secret-key');
    log('Token verified for user:', decoded.userId);
    
    // Add user ID to context for use in the handler
    context.userId = decoded.userId;
    return decoded.userId;
  } catch (error) {
    log('Token verification failed:', error.message);
    return null;
  }
};

/**
 * Middleware to protect routes that require authentication
 * @param {Function} handler - The route handler function
 * @returns {Function} - Protected route handler
 */
const requireAuth = (handler) => async (event, context) => {
  // Set CORS headers
  const headers = {
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Headers': 'Content-Type, Authorization',
    'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
    'Content-Type': 'application/json',
  };

  // Handle preflight OPTIONS request
  if (event.httpMethod === 'OPTIONS') {
    return {
      statusCode: 200,
      headers,
      body: '',
    };
  }

  // Verify token
  const userId = verifyToken(event, context);
  
  if (!userId) {
    return {
      statusCode: 401,
      headers,
      body: JSON.stringify({ error: 'Unauthorized - Invalid or missing token' }),
    };
  }
  
  // If authenticated, proceed to the handler
  return handler(event, context);
};

module.exports = {
  verifyToken,
  requireAuth,
  getTokenFromRequest
};
